<template>
<div>
    <div>
  <router-view :key="$route.fullPath"/>
    <v-container fluid grid-list-md >
      <v-layout row wrap >
        <v-flex sm12 md6 lg4 xl3 v-for="recipe in paginated_items[currentPage-1]" :key="recipe.recipe_id" style="margin:auto%" >
          <my-recipe-list-item :recipe="recipe"></my-recipe-list-item>
        </v-flex>
      </v-layout>
    </v-container>
    <b-row>
        <b-pagination id="pagination" :total-rows="totalRows()" :per-page="perPage" v-model="currentPage" class="my-0" />
    </b-row>
    <div :key="flag"></div>
  </div>
</div>
</template>

<script>
import { mapState, mapActions, mapMutations } from 'vuex'
import MyRecipeListItem from './MyRecipeListItem.vue'

export default {
    name: 'LikeRecipeList',
    props:{

    },
    data() {
        return {
          flag: false,
          currentPage: 1,
          perPage: 6,
          paginated_items: [],
          currentPageIndex:0,
          nbPages:0,
        }
    },
    components: {
        MyRecipeListItem
    },
    computed: {
        ...mapState('myblog', ['likerecipes']),
    },
    methods: {
       ...mapActions('myblog', ['selectedRecipe', 'fetchLikeRecipes']),
       ...mapActions('recipes', ['fetchRecipe']),
       ...mapMutations('myblog',['SET_FLAG']),
      changePaginateditems(index, value) {
        this.paginated_items[index] = value
      },
      increasenbpages() {
        this.nbPages++
      },
      fortest(){
        if(this.flag ===false){
          this.$router.go(0)
          this.flag = true
        }
      },
      currentPageItems() {
          let lengthAll =this.likerecipes.length;
          for (let i = 0; i < lengthAll; i = i + this.perPage) {
            this.changePaginateditems(this.nbPages, this.likerecipes.slice(i,i+this.perPage))
            this.increasenbpages()
          }
        },
        totalRows() {
            return this.likerecipes.length
      }
    },
    watch: {
      likerecipes: {
        deep: true,
        handler() {
          this.paginated_items = []
          let lengthAll =this.likerecipes.length;
          for (let i = 0; i < lengthAll; i = i + this.perPage) {
            this.paginated_items.push(this.likerecipes.slice(i,i+this.perPage))
            this.nbPages++
          }
        }
      },
      paginated_items: {
        deep:true,
        handler() {
          this.currentPageItems();
          this.totalRows();
        }
      }
    },
    mounted() {
      this.SET_FLAG(false)
    },
    updated() {
      // this.currentPageItems();
      // this.totalRows();
    },
    created() {
      this.fetchLikeRecipes()
      // this.currentPageItems();
      // this.totalRows();
    },
}
</script>

<style>
     #my-recipes:hover {
        cursor: pointer;
        color: orange;
    }
     #pagination {
      margin-left: auto;
      margin-right: auto;
    }
    .page-item.active .page-link {
    z-index: 3;
    color: #fff;
    background-color: grey !important;
    border-color: lightgrey !important;
    }
</style>